@charset "UTF-8";
////////////////////////
// title-center
////////////////////////

@use '../../global' as g;

.c-title-center{
	text-align: center;
	b{
		position: relative;
		display: block;
		height: g.pc_vw(20);
		margin-bottom: g.pc_vw(28);
		@include g.before{
			display: inline-block;
			width: g.pc_vw(20);
			height: g.pc_vw(20);
			border-radius: 50%;
			background: #fff;
			animation:icon-blink 4s g.$easeInOutSine infinite alternate;
		}
	}
	span{
		display: block;
		@include g.font_pp_r;
		@include g.fs(18,24);
		@include g.ls(30);
	}
	@include g.sp{
		b{
			height: g.sp_vw(20);
			margin-bottom: g.sp_vw(28);
			@include g.before{
				display: inline-block;
				width: g.sp_vw(20);
				height: g.sp_vw(20);
			}
		}
		span{
			@include g.fs(18,24,true,false);
			@include g.ls(30);
		}
	}
}
