@charset "UTF-8";
////////////////////////
// movieModal
////////////////////////

@use '../../global' as g;

.p-movieModal{
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: g.zIndex(movieModal);
	background-color: rgba($color: #000000, $alpha: 0.7);
	display: none;
}

.p-movieModal__inner{
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	width: 640px;
	height: 360px;
	background: #000;
	iframe{
		width: 100%;
		height: 100%;
	}
	@include g.sp{
		width: 100%;
		height: calc(100vw * 9 / 16);
	}
}

.p-movieModal__inner2{
	position: relative;
	width: 100%;
	height: 100%;
}

.p-movieModal__close{
	position: absolute;
	@include g.pc{
		width: g.pc_vw(50);
		height: g.pc_vw(50);
		right: g.pc_vw(-50);
		top: g.pc_vw(-50);
	}
	@include g.sp{
		width: g.sp_vw(30);
		height: g.sp_vw(30);
		right: g.sp_vw(0);
		top: g.sp_vw(-30);
	}
}
