@charset "UTF-8";
////////////////////////
// Cursor
////////////////////////

@use '../../global' as g;

.c-cursor{
  pointer-events: none;
  position: fixed;
  top: -100px;
  left: -100px;
	z-index: g.zIndex(cursor);
  // @include g.sp{
  //   display: none;
  // }
}

.c-cursor__normal{
  position: absolute;
  top: -5px;
  left: -5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #FFF;
  transition-duration: 0.2s;
  transition-property: transform opacity;
  &.is-hover{
    transform: scale(3, 3);
    opacity: 0.5;
    transition-duration: 0.4s;
    transition-timing-function: cubic-bezier(0.175,  1.885, 0.320, 1.275);
  }
  &.hide{
    transform: scale(0, 0);
    opacity: 0.0;
  }
}

.c-cursor__circle{
  position: absolute;
  top: -28px;
  left: -28px;
  width: 56px;
  height: 56px;
  background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/cursor_circle.svg) 0 0 no-repeat;
  background-size: 100% auto;
}

.c-cursor__arrow{
  position: absolute;
  top: -27px;
  left: -27px;
  width: 54px;
  height: 54px;
  background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/cursor_arrow.svg) 0 0 no-repeat;
  background-size: 100% auto;
}

.c-cursor__text{
  position: absolute;
  top: 0;
  left: 50%;
  margin-top: -12px;
  @include g.font_pp_m;
  font-size: 18px;
  line-height: 24px;
  @include g.ls(30);
  white-space: nowrap;
  overflow: hidden;
  >span{
    display: inline-block;
  }

  &--slide{
    @include g.font_noto_r;
    top: 35px;
    margin-top: 0;
    font-size: 14px;
    line-height: 20px;
    @include g.ls(0);
    transform: translateX(-50%);
  }
}


