@charset "UTF-8";
////////////////////////
// case-studies-detail
////////////////////////
@use "sass:math";
@use '../global' as g;

.pg-case-studies-detail{
	position: relative;
}

.pg-case-studies-detail__bg{
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: g.pc_vw(900);
	@include g.sp{
		height: g.sp_vw(700);
	}
	@include g.after{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(rgba(13, 13, 13, 0) 0%, rgba(13, 13, 13, 0.68) 50%, rgba(13, 13, 13, 0.91) 85.71%, rgba(0, 0, 0, 0.91) 100%);
	}
}

.pg-case-studies-detail__bgInner{
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto;
	img{
		object-fit: cover;
		height: 100%;
	}

}

// kv
.pg-case-studies-detail__kv{
	// background: linear-gradient(rgba(13, 13, 13, 0) 0%, rgba(13, 13, 13, 0.68) 50%, rgba(13, 13, 13, 0.91) 85.71%, rgba(0, 0, 0, 0.91) 100%);
	display: flex;
	align-items: flex-end;
	width: 100%;
	@include g.pc{
		height: g.pc_vw(900);
	}
	@include g.sp{
		height: g.sp_vw(700);
	}
}

.pg-case-studies-detail__kvInner{
	@include g.pc{
		padding-bottom: g.pc_vw(50);
		padding-left: g.pc_vw(50);
		width: g.pc_vw(675);
	}
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(80);
	}
}

.pg-case-studies-detail__title{
	border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	@include g.pc{
		padding-bottom: g.pc_vw(30);
		margin-bottom: g.pc_vw(80);
	}
	@include g.sp{
		padding-bottom: g.sp_vw(20);
		margin-bottom: g.sp_vw(40);
	}
}


.pg-case-studies-detail__category{
	@include g.font_pp_r;
	@include g.pc{
		@include g.fs(18, 18, true);
		@include g.ls(30);
		margin-bottom: g.pc_vw(30);
	}
	@include g.sp{
		@include g.fs(18, 18, true, false);
		@include g.ls(30);
		margin-bottom: g.sp_vw(30);
	}
}

.pg-case-studies-detail__subTitle{
	@include g.font_pp_m;
	@include g.pc{
		@include g.fs(51.84, 59.6, true);
		@include g.ls(30);
		margin-bottom: g.pc_vw(30);
	}
	@include g.sp{
		@include g.fs(40, 45, true, false);
		@include g.ls(30);
		margin-bottom: g.sp_vw(30);

	}
}

.pg-case-studies-detail__description{
	opacity: 0.4;
	@include g.pc{
		@include g.fs(14, 24, true);
		@include g.ls(100);
		width: g.pc_vw(430);
	}
	@include g.sp{
		@include g.fs(14, 24, true, false);
		@include g.ls(100);
	}
}

.pg-case-studies-detail__links{
	margin-top: g.pc_vw(40);
	@include g.sp{
		margin-top: g.sp_vw(26);
	}
}

// body
.pg-case-studies-detail__body{
	// background: rgba(0, 0, 0, 0.91);
	padding-top: g.pc_vw(80);
	@include g.sp{
		padding-top: g.sp_vw(80);
	}
}


// under
.pg-case-studies-detail__under{
	padding: g.pc_vw(60) 0 0;
	background: #000;
	@include g.sp{
		padding: 0 0 0;
	}
}

// staff
.pg-case-studies-detail__staff{
	padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(190);
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(80);
	}
}

// pager
.pg-case-studies-detail__pager{
	padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(60);
	@include g.sp{
		padding: 0 g.sp_vw(80) g.sp_vw(80);
	}
}

// related
.pg-case-studies-detail__related{
	padding: 0 g.pc_vw(50) 0;
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(0);
	}
}

.pg-case-studies-detail__relatedTitle{
	padding-bottom: g.pc_vw(30);
	border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	margin-bottom: g.pc_vw(60);
	@include g.sp{
		padding-bottom: g.sp_vw(20);
		margin-bottom: g.sp_vw(30);
	}
}

.pg-case-studies-detail__relatedBody{
	@include g.pc{
		margin-bottom: g.pc_vw(-30);
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	@include g.sp{

	}
}

.pg-case-studies-detail__article{
	position: relative;
	overflow: hidden;
	@include g.pc{
		margin-bottom: g.pc_vw(30);
	}
	@include g.sp{
		&:not(:last-of-type){
			margin-bottom: g.sp_vw(30);
		}
	}
}

.pg-case-studies-detail__articleLink{
	display: block;
	@include g.pc{
		height: 100%;
	}
}

.pg-case-studies-detail__articlePhoto{
	position: relative;
	overflow: hidden;
	border-radius: g.pc_vw(20);
	img{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		object-fit: cover;
		transition: opacity 0.3s, transform 0.3s;
	}
	@include g.pc{
		width: g.pc_vw(650);
		height: g.pc_vw(math.div(650 * 900 , 1440));
		img{
			opacity: 0.5;

		}
		.pg-case-studies-detail__articleLink:hover & img{
			transform: scale(1.05);
			opacity: 1;
		}
	}
	@include g.sp{
		width: g.sp_vw(335);
		height: g.sp_vw(math.div(335 * 900 , 1440));
		margin-bottom: g.sp_vw(25);
		border-radius: g.sp_vw(20);
	}
}

.pg-case-studies-detail__articleInfo{
	@include g.pc{
		position: absolute;
		left: 0;
		top:0;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: flex-end;
		flex-direction: column;
		padding: 0 g.pc_vw(70) g.pc_vw(20) g.pc_vw(20);
	}
	@include g.sp{
		padding: 0 g.sp_vw(35) 0 0;
	}
}

.pg-case-studies-detail__articleCategory{
	@include g.font_pp_r;
	@include g.pc{
		@include g.fs(12, 12, true);
		@include g.ls(30);
	}
	@include g.sp{
		@include g.fs(12, 18, true, false);
		@include g.ls(30);
	}
}

.pg-case-studies-detail__articleTitle{
	@include g.font_pp_r;
	@include g.pc{
		@include g.fs(24, 30, true);
		@include g.ls(30);
		margin-top: g.pc_vw(15);
	}
	@include g.sp{
		@include g.fs(18, 24, true,false);
		@include g.ls(30);
		margin-top: g.sp_vw(10);
	}
}

.pg-case-studies-detail__articleArrow{
	position: absolute;
	i{
		display: block;
		width: 100%;
		height: 100%;
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/diagonal-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
	@include g.sp{
		width: g.sp_vw(20);
		height: g.sp_vw(20);
		right: g.sp_vw(0);
		bottom: g.sp_vw(0);
	}
}
