@charset "UTF-8";
////////////////////////
// careers
////////////////////////
@use "sass:math";
@use '../global' as g;

.pg-careers-detail{
	position: relative;
	@include g.pc{
		margin-bottom: g.pc_vw(80);
	}
	@include g.sp{
		margin-bottom: g.sp_vw(80);
	}
}


.pg-careers-detail__kv{
	@include g.pc{
		display: flex;
		align-items: center;
		padding: 0 g.pc_vw(165) 0 g.pc_vw(150);
		height: g.pc_vw(900);
	}
	@include g.sp{
		overflow: hidden;
	}
}

.pg-careers-detail__kvPhoto{
	@include g.pc{
		flex:1;
		padding-left: g.pc_vw(50);
	}
	@include g.sp{
		margin-top: g.sp_vw(70);
	}
}

.pg-careers-detail__kvPhotoInner{
	img{
		box-shadow: g.pc_vw(20) g.pc_vw(20) g.pc_vw(99) rgba(0, 0, 0, 0.8);
	}
	@include g.pc{
		$width:500;
		position: relative;
		width: g.pc_vw($width);
		height: g.pc_vw(math.div(1510, 1080) * $width);
		transform-origin: right bottom;
		perspective: 80vw;
    transform-style: preserve-3d;
		img{
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			border-radius: g.pc_vw(20);
			transform: rotate(10deg);
		}
	}

	@include g.sp{
		$width:400;
		width: g.sp_vw($width);
		height: g.sp_vw(math.div(1510, 1080) * $width);
		transform-origin: center;
		position: relative;
		transform: translate(0,0) !important;
		img{
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			border-radius: g.sp_vw(40);
			transform: rotate(10deg) rotateY(0) !important;
		}
	}
}

.pg-careers-detail__kvInfo{
	@include g.pc{
		width: g.pc_vw(430);
	}
	@include g.sp{
		margin: g.sp_vw(20) g.sp_vw(g.$spMargin) 0;
	}
}

.pg-careers-detail__kvInfoInner{

}
.pg-careers-detail__KvTitle{
	border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	padding-bottom: g.pc_vw(30);
	margin-bottom: g.pc_vw(40);
	@include g.sp{
		padding-bottom: g.sp_vw(20);
		margin-bottom: g.sp_vw(20);
	}
}

.pg-careers-detail__kvSubTitle{
	@include g.font_noto_m;
	@include g.fs(28, 42, true);
	@include g.ls(60);
	margin-bottom: g.pc_vw(55);
	@include g.sp{
		@include g.fs(24, 36, true, false);
		margin-bottom: g.sp_vw(40);
	}
}

.pg-careers-detail__kvName{
	@include g.fs(28, 38, true);
	@include g.ls(30);
	margin-bottom: g.pc_vw(10);
	@include g.sp{
		@include g.fs(24, 34, true, false);
		margin-bottom: g.sp_vw(10);
	}
}

.pg-careers-detail__kvPosition{
	@include g.fs(18, 18, true);
	@include g.ls(30);
	margin-bottom: g.pc_vw(10);
	@include g.sp{
		margin-bottom: g.sp_vw(10);
		@include g.fs(16, 16, true, false);
	}
}

.pg-careers-detail__kvCategory{
	@include g.fs(12, 12, true);
	@include g.ls(30);
	@include g.sp{
		@include g.fs(12, 12, true, false);
	}
}

// body
.pg-careers-detail__body{
	margin-bottom: g.pc_vw(70);
	padding-top: g.pc_vw(100);
	@include g.sp{
		padding-top: g.sp_vw(100);
	}
}

.pg-careers-detail__interview{
	@include g.sp{

	}
}

.pg-careers-detail__interviewTitle{
	margin: 0 g.pc_vw(50) g.pc_vw(40);
	@include g.sp{
		margin: 0 g.sp_vw(g.$spMargin) g.sp_vw(40);
	}
}

// under
.pg-careers-detail__under{
	padding: g.pc_vw(60) 0 0;
	background: #000;
}

// staff
.pg-careers-detail__staff{
	padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(190);
}

// pager
.pg-careers-detail__pager{
	padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(80);
	@include g.sp{
		padding: 0 g.sp_vw(80) g.sp_vw(80);
	}
}
