@charset "UTF-8";
////////////////////////
// slider
////////////////////////

@use '../../global' as g;

.c-slider{
	position: relative;
  display: block;
	overflow-x: hidden;
	padding-bottom: g.pc_vw(180);
	@include g.sp{
		padding-bottom: g.sp_vw(120);
	}
}

.c-slider__listContainer{
	position: relative;
	width: 100%;
	height: g.pc_vw(590);
	@include g.sp{
		height: g.sp_vw(440);
	}
}

.c-slider__list{
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	padding: 0 g.pc_vw(85);
	@include g.sp{
		padding: 0 g.sp_vw(23);
	}
}

.c-slider__listItem{
	width: g.pc_vw(360);
	flex-shrink: 0;
	margin-right: g.pc_vw(95);
	@include g.sp{
		width: g.sp_vw(268);
		margin-right: g.sp_vw(22);
	}

	&:nth-child(even){
		transform: translateY(g.pc_vw(80));
		@include g.sp{
			transform: translateY(g.sp_vw(60));
		}
	}
	&:last-child{
		margin-right: 0;
	}
}

.c-slider__ui{
	position: absolute;
	top: 0;
	display: block;
	width: g.pc_vw(100);
	height: 100%;
	@include g.sp{
		display: none;
	}

	&--prev{
		left: 0;
	}
	&--next{
		right: 0;
	}
}

.c-slider__scrollbar{
	position: absolute;
	bottom: 0;
	left: g.pc_vw(50);
	width: g.pc_vw(1340);
	height: g.pc_vw(40);
	@include g.before{
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: rgba($color: #FFF, $alpha: 0.2);
	}
	@include g.sp{
		left: g.sp_vw(23);
		width: g.sp_vw(324);
		height: g.sp_vw(40);
	}
}

.c-slider__scrollbarThumb{
	position: absolute;
	left: 0;
	top: 0;
	display: block;
	height: 100%;
	@include g.after{
		pointer-events: none;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: #FFF;
	}
}

