@charset "UTF-8";
////////////////////////
// article
////////////////////////

@use '../../global' as g;

.c-article{
	// border-bottom: 1px solid rgba(#fff, 0.2);
}

.c-article__link{
	position: relative;
	display: flex;
	@include g.before{
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 1px;
		background: rgba(#fff,0.2);
		transform: translate3d(0,0,0);
	}
	@include g.sp{
		flex-wrap:wrap;
	}
}

.c-article__time{
	time{
		display: block;
	}
	span{
		@include g.font_pp_r;
		display: block;
		text-align: center;
		&:nth-of-type(1){
			opacity: 0.4;
		}
		&:nth-of-type(3){
			position: relative;
			opacity: 0.4;
			@include g.after{
				height: 1px;
				transform: rotate(-45deg);
				background: #fff;
			}
		}
	}
	@include g.pc{
		width: g.pc_vw(115);
		time{
			margin: g.pc_vw(22) g.pc_vw(0) g.pc_vw(0) g.pc_vw(30);
		}
		span{
			@include g.ls(30);
			&:nth-of-type(1){
				@include g.fs(12, 12, true);
				margin-bottom: g.pc_vw(8);
			}
			&:nth-of-type(2){
				@include g.fs(24, 24, true);
				margin-bottom: g.pc_vw(3);
			}
			&:nth-of-type(3){
				@include g.after{
					margin: g.pc_vw(10) auto;
					width: g.pc_vw(12);
				}
			}
			&:nth-of-type(4){
				@include g.fs(24, 24, true);
			}
		}
	}
	@include g.sp{
		width: g.sp_vw(55);
		time{
			margin: g.sp_vw(22) g.sp_vw(0) g.sp_vw(0) g.sp_vw(0);
		}
		span{
			@include g.ls(30);
			&:nth-of-type(1){
				@include g.fs(12, 12, true, false);
				margin-bottom: g.sp_vw(8);
			}
			&:nth-of-type(2){
				@include g.fs(24, 24, true, false);
				margin-bottom: g.sp_vw(3);
			}
			&:nth-of-type(3){
				@include g.after{
					margin: g.sp_vw(10) auto;
					width: g.sp_vw(12);
				}
			}
			&:nth-of-type(4){
				@include g.fs(24, 24, true, false);
			}
		}
	}
}

.c-article__photo{
	position: relative;
	overflow: hidden;
	margin-bottom: 1px;
	@include g.before{
		width: 100%;
		height: 0;
		padding-top: calc( 9 / 16 * 100%);
	}
	img{
		position: absolute;
		left: 0;
		top:0;
		right: 0;
		bottom:0;
		margin:auto;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transition: opacity 0.3s, transform 0.3s;
	}
	@include g.pc{
		width: g.pc_vw(310);
		.c-article__link:hover &{
			img{
				transform: scale(1.05);
				opacity: 0.5;
			}
		}
	}
	@include g.sp{
		width: calc(100% - g.sp_vw(55));
	}
}

.c-article__info{
	position: relative;
	padding: g.pc_vw(20) g.pc_vw(112) g.pc_vw(20) g.pc_vw(30);
	flex:1;
	@include g.sp{
		width: 100%;
		padding: g.sp_vw(22) 0 g.sp_vw(21) g.sp_vw(55);
	}
}

.c-article__category{
	@include g.fs(12,12,true);
	@include g.ls(30);
	margin-bottom: g.pc_vw(10);
	opacity: 0.4;
	@include g.sp{
		@include g.fs(12,12,true,false);
		margin-bottom: g.sp_vw(13);
	}
}

.c-article__title{
	@include g.pc{
		@include g.fs(24,34,true);
		@include g.ls(60);
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	@include g.sp{
		@include g.fs(16,24,true, false);
		@include g.ls(30);
		-webkit-line-clamp: 3;
	}
}

.c-article__description{
	@include g.ls(30);
	opacity: 0.4;
	@include g.pc{
		@include g.fs(12,22,true);
		margin-top: g.pc_vw(15);
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	@include g.sp{
		@include g.fs(12,18,true,false);
		margin-top: g.sp_vw(12);
		margin-right: g.sp_vw(32);
		min-height: g.sp_vw(25);
	}
}

.c-article__arrow{
	width: g.pc_vw(30);
	height: g.pc_vw(30);
	position: absolute;
	i{
		display: block;
		width: 100%;
		height: 100%;
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/diagonal-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
	@include g.pc{
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
	}
	@include g.sp{
		width: g.sp_vw(20);
		height: g.sp_vw(20);
		right: 0;
		bottom: g.sp_vw(20);
	}
}

.c-article__line{
	position: absolute;
	left: 0;
	bottom: 0px;
	width: 100%;
	height: 1px;
	background: g.$gradient;
	position: absolute;
	transform: scaleX(0) translateZ(0);
	transform-origin: 0 0;
	z-index: 1;
}
