@charset "UTF-8";
////////////////////////
// pagerNumber
////////////////////////

@use '../../global' as g;

.c-pagerNumber{
  display: flex;
	justify-content: center;
	align-items: center;
	@include g.sp{
		margin-bottom: g.sp_vw(30);
	}
}

.c-pagerNumber__number{
	@include g.font_pp_r;
	@include g.hover(){
		opacity: g.$fadeOpacity;
	}
	@include g.fs(24, 24, true);
	transition: opacity g.$fadeDuration;
	margin-right: g.pc_vw(20);
	@include g.sp{
		@include g.fs(18, 18, true, false);
		margin-right: g.sp_vw(20);
	}
}

.c-pagerNumber__number--current{
	pointer-events: none;
	opacity: g.$fadeOpacity;
}

.c-pagerNumber__dot{
	@include g.fs(24, 24, true);
	margin-right: g.pc_vw(20);
	@include g.sp{
		@include g.fs(18, 18, true, false);
		margin-right: g.sp_vw(20);
	}
}
