@charset "UTF-8";
@use 'sass:math';
@use '../functions' as f;
//////////////////////////////////////////
// font-size
//////////////////////////////////////////

@mixin fs($font-size, $line-height:null, $lineHeightCrop:true, $isPc:true){
  $fontSizeVw: null;
  @if($isPc == true){
    $fontSizeVw: f.pc_vw($font-size);
  }@else{
    $fontSizeVw: f.sp_vw($font-size);
  }
  font-size: $fontSizeVw;

  @if($line-height!=null){
    line-height: math.div($line-height, $font-size);
  }

  @if($lineHeightCrop == true){
    &::before,
    &::after{
      content:"";
      display:block;
      height: 0;
      width: 0;
    }

    $percent: null;
    @if($isPc == true){
      $percent: f.pc_vw($line-height - $font-size);
    }@else{
      $percent: f.sp_vw($line-height - $font-size);
    }

    &::before{
      margin-bottom: $percent * -0.5;
    }
    &::after{
      margin-top: $percent * -0.5;
    }
  }

}
