@charset "UTF-8";
////////////////////////
// notFound
////////////////////////

@use '../global' as g;

.pg-notFound{
	position: relative;
	padding: g.pc_vw(380) 0 g.pc_vw(220);
	@include g.sp{
		padding: g.sp_vw(280) 0 g.sp_vw(200);
	}
}

.pg-notFound__body{
	padding-left: g.pc_vw(165);
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) 0;
	}
}

.pg-notFound__title{
	margin-bottom: g.pc_vw(60);
	@include g.fs(28,28,true);
	@include g.ls(60);
	@include g.sp{
		margin-bottom: g.sp_vw(60);
		@include g.fs(18,18,true,false);
		@include g.ls(60);
	}
}

.pg-notFound__btn{
	width: g.pc_vw(310);
	@include g.sp{
		width: g.sp_vw(210);
	}
}
