@charset "UTF-8";
////////////////////////
// btn
////////////////////////

@use '../../global' as g;

// common
.c-btn{
	position: relative;
	text-decoration: none;
	display: flex;
	justify-content: space-between;
	@include g.before{
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 1px;
		background: rgba(#fff,0.2);
		transform: translate3d(0,0,0);
	}
}

.c-btn__text{
	@include g.font_pp_r;
	@include g.ls(30);
}

.c-btn__arrow{
	position: relative;
	// overflow: hidden;
	i{
		display: block;
		width: 100%;
		height: 100%;
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/diagonal-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
	s{
		display: block;
		width: 100%;
		height: 100%;
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/under-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
}

.c-btn__line{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 1px;
	background: g.$gradient;
	position: absolute;
	transform: scaleX(0) translateZ(0);
	transform-origin: 0 0;
	z-index: 1;
}

// large
.c-btn--large{
	padding: 0 0 g.pc_vw(46);
	@include g.sp{
		padding: 0 0 g.sp_vw(25);
	}
	.c-btn__text{
		@include g.fs(24, 24, false);
		@include g.sp{
			@include g.fs(18, 18, false, false);
		}
	}
	.c-btn__arrow{
		width: g.pc_vw(30);
		height: g.pc_vw(30);
		@include g.sp{
			width: g.sp_vw(15);
			height: g.sp_vw(15);
		}
	}
}

// small
.c-btn--small{
	padding: 0 0 g.pc_vw(20);
	@include g.sp{
		padding: 0 0 g.sp_vw(20);
	}
	.c-btn__text{
		@include g.fs(18, 18, false);
		@include g.sp{
			@include g.fs(18, 18, false, false);
		}
	}
	.c-btn__arrow{
		width: g.pc_vw(15);
		height: g.pc_vw(15);
		@include g.sp{
			width: g.sp_vw(15);
			height: g.sp_vw(15);
		}
	}
}

