@charset "UTF-8";
////////////////////////
// base
////////////////////////

@use '../global' as g;

/////////
// PP Neue Montreal
/////////


@font-face {
  // thin
  font-family: 'PP Neue Montreal';
  font-style: normal;
  font-display: swap;
  font-weight: 450;
  src: url('/wp-content/themes/theme/o-e-n.co.jp/_assets/fonts/PPNeueMontreal-Regular.woff2') format('woff2'),
        url('/wp-content/themes/theme/o-e-n.co.jp/_assets/fonts/PPNeueMontreal-Regular.woff') format('woff');
}

@font-face {
  // thin
  font-family: 'PP Neue Montreal';
  font-style: normal;
  font-display: swap;
  font-weight: 530;
  src: url('/wp-content/themes/theme/o-e-n.co.jp/_assets/fonts/PPNeueMontreal-Medium.woff2') format('woff2'),
        url('/wp-content/themes/theme/o-e-n.co.jp/_assets/fonts/PPNeueMontreal-Medium.woff') format('woff');
}


/////////
// other
/////////

html {
  height: 100%;
  font-size: 62.5%;
	@include g.pc{
		cursor: none;
	}
}

body {
  text-size-adjust: 100%;
  height: 100%;
  font-family: YakuHanJP, 'Noto Sans JP', -apple-system, BlinkMacSystemFont, "Segoe UI", "Hiragino Sans", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
	font-style: normal;
  font-weight: 400;
  color: #fff;
	background: #000;
  font-feature-settings: 'palt';
  -webkit-font-smoothing: antialiased;
}

a[href]{
  color:#fff;
	text-decoration: none;
	@include g.pc{
		cursor: none;
	}
}

button{
	@include g.pc{
		cursor: none;
	}
}

select{
	@include g.pc{
		cursor: none;
	}
}

img{
  width: 100%;
	height: auto;
  vertical-align: bottom;
}

a[href^="tel:"]{
  pointer-events: none;
}

