@charset "UTF-8";
////////////////////////
// links
////////////////////////

@use '../../global' as g;

.c-links{
	li{
		&:not(:last-of-type){
			margin-bottom: g.pc_vw(8);
		}
	}
	a{
		@include g.fs(12, 18, false);
		opacity: 0.4;
		transition: opacity 0.2s;
		display: flex;
		align-items: center;
		@include g.hover{
			opacity: 1;
		}
	}
	span{
		display: inline-block;
		margin-left: g.pc_vw(10);
	}
	b{
		width: g.pc_vw(12);
		height: g.pc_vw(12);
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/blank.svg) center no-repeat;
		background-size: 100% auto;
	}
	@include g.sp{
		li{
			&:not(:last-of-type){
				margin-bottom: g.sp_vw(10);
			}
		}
		a{
			@include g.fs(12, 18, false, false);
		}
		span{
			margin-left: g.sp_vw(10);
		}
		b{
			width: g.sp_vw(12);
			height: g.sp_vw(12);
		}
	}
}
