@charset "UTF-8";
////////////////////////
// articleSection
////////////////////////

@use '../../global' as g;

.p-articleSection{
	margin: 0 g.pc_vw(g.$pcMargin) 0;
	padding-bottom: g.pc_vw(80);
	@include g.sp{
		margin: 0 g.sp_vw(g.$spMargin) 0;
		padding-bottom: g.sp_vw(100);
	}
}

.p-articleSection__title{
	@include g.fs(24, 34, true);
	@include g.ls(60);
	@include g.font_noto_m;
	margin-bottom: g.pc_vw(45);
	@include g.sp{
		@include g.fs(24, 34, true, false);
		margin-bottom: g.sp_vw(80);
	}
}

.p-articleSection__photo{
	&.center{
    text-align: center;
	}
	&.left{
    text-align: left;
	}
	&.right{
    text-align: right;
	}
	a{
		display: block;
		position: relative;
	}
	img{
		width: auto;
		max-width: 100%;
		height: auto;
	}
	a[href]{
		transition: opacity 0.4s;
		@include g.hover{
			opacity: 0.6;
		}
	}
	&.small{
		img{
			width: 30%;
		}
	}
	&.medium{
		img{
			width: 60%;
		}
	}
	&.large{
		img{
			width: 100%;
		}
	}
	&.swiper-slide{
		img{
			width: 100%;
		}
	}
	&.wide{
		width: g.pc_vw(1340);
		margin-left: g.pc_vw(-230);
		img{
			width: 100%;
		}
	}
	@include g.sp{
		&.small{
			img{
				width: 45%;
			}
		}
		&.medium{
			img{
				width: 75%;
			}
		}
		&.large{
			img{
				width: 100%;
			}
		}
		&.wide{
			margin-left: g.sp_vw(-1 * g.$spMargin);
			width: g.sp_vw(375);
		}
	}
}


.p-articleSection__caption{
	@include g.fs(14, 22, true);
	@include g.ls(100);
	margin-top: g.pc_vw(15);
	color: #666;
	&:empty{
		margin-top: g.pc_vw(0);
	}
	&.center{
    text-align: center;
	}
	&.left{
    text-align: left;
	}
	&.right{
    text-align: right;
	}
	&:not(:last-child){
		margin-bottom: g.pc_vw(80);
	}
	@include g.sp{
		@include g.fs(14, 22, true, false);
		margin-top: g.sp_vw(15);
		&:not(:last-child){
			margin-bottom: g.sp_vw(80);
		}
	}
}

.p-articleSection__text{
	@include g.fs(16, 30, true);
	@include g.ls(80);
	&:not(:last-child){
		margin-bottom: g.pc_vw(80);
	}
	p:not(:last-child){
		margin-bottom: g.pc_vw(30);
	}
	blockquote{
		display: block;
		border-left: g.pc_vw(5) solid #666;
		padding-left: g.pc_vw(30);
		color: #666;
		&:not(:last-child){
			margin-bottom: g.pc_vw(30);
		}
	}
	strong{
		font-weight: bold;
	}
	a{
		@include g.font_noto_m;
		text-decoration: underline;
		@include g.hover{
			text-decoration: none;
		}
	}
	&.small{
		@include g.fs(14, 26, true);
		@include g.ls(100);
		color: #666;
	}
	@include g.sp{
		@include g.fs(16, 30, true, false);
		&:not(:last-child){
			margin-bottom: g.sp_vw(80);
		}
		p:not(:last-child){
			margin-bottom: g.sp_vw(30);
		}
		blockquote{
			border-left: g.sp_vw(3) solid #666;
			padding-left: g.sp_vw(20);
			&:not(:last-child){
				margin-bottom: g.sp_vw(30);
			}
		}
		&.small{
			@include g.fs(14, 26, true, false);
		}
	}
}


.p-articleSection__iframe{
	position: relative;
	padding-top: 56.25%;
	&:not(:last-child){
		margin-bottom: g.pc_vw(80);
	}
	iframe{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}
	@include g.sp{
		&:not(:last-child){
			margin-bottom: g.sp_vw(80);
		}
	}
}

.p-articleSection__slider{
	position: relative;
	overflow: hidden;
	padding-bottom: g.pc_vw(120);
	&:last-child{
		padding-bottom: g.pc_vw(80);
	}
	@include g.sp{
		padding-bottom: g.sp_vw(120);
		&:last-child{
			padding-bottom: g.sp_vw(50);
		}
	}
}

.p-articleSection__sliderInner{
	position: relative;
	overflow: visible !important;
}

.p-articleSection__sliderNav{
	background: #000;
	display: flex;
	width: fit-content;
	position: absolute;
	right: 0;
	bottom: g.pc_vw(-15);
	transform: translateY(100%);
	z-index: 1;
	@include g.sp{
		bottom: g.sp_vw(-17);
		left:0;
		margin:auto;
	}
}

.p-articleSection__sliderBtn{
	width: g.pc_vw(28);
	height: g.pc_vw(28);
	&--prev{
		margin-right: g.pc_vw(20);
	}
	&.swiper-button-disabled{
		opacity: 0.4;
	}
	&.swiper-button-lock{
		opacity: 0;
	}
	@include g.sp{
		width: g.sp_vw(28);
		height: g.sp_vw(28);
		&--prev{
			margin-right: g.sp_vw(20);
		}
	}
}

.p-articleSection__sliderCaption{
	@include g.fs(14, 20, true);
	@include g.ls(100);
	color: #666;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
	@include g.pc{
		position: absolute;
		left:0;
		bottom: g.pc_vw(-15);
		transform: translateY(100%);
		padding-right: g.pc_vw(95);
	}
	@include g.sp{
		margin-top: g.sp_vw(15);
		@include g.fs(14, 20, true, false);
	}
}
