@charset "UTF-8";
//////////////////////////////////////////
// font-family
//////////////////////////////////////////

@mixin font_noto_r{
	font-family: YakuHanJP, 'Noto Sans JP', sans-serif;
  font-weight: 400;
}

@mixin font_noto_m{
	font-family: YakuHanJP, 'Noto Sans JP', sans-serif;
  font-weight: 500;
}

@mixin font_pp_r{
	font-family: 'PP Neue Montreal', YakuHanJP, 'Noto Sans JP', sans-serif;
  font-weight: 450;
}

@mixin font_pp_m{
	font-family: 'PP Neue Montreal', YakuHanJP, 'Noto Sans JP', sans-serif;
  font-weight: 530;
}
