@charset "UTF-8";
////////////////////////
// staffList
////////////////////////
@use "sass:math";
@use '../../global' as g;


.p-staffList{
}

.p-staffList__title{
	@include g.fs(24, 34, true);
	@include g.ls(30);
	margin-bottom: g.pc_vw(45);
	@include g.sp{
		@include g.fs(24, 34, true, false);
		margin-bottom: g.sp_vw(40);
	}
}

.p-staffList__list{
	@include g.pc{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
}

.p-staffList__listChild{
	dl{
		display: flex;
	}
	dt{

		color:#666;
	}
	dd{
		flex:1;
	}
	@include g.pc{
		width: calc( 50% - g.pc_vw(15));
		dl{
			margin-bottom: g.pc_vw(25);
		}
		dt{
			@include g.fs(16, 28, true);
			@include g.ls(80);
			width: g.pc_vw(90);
			margin-right: g.pc_vw(20);
		}
		dd{
			@include g.fs(16, 28, true);
			@include g.ls(80);
		}
	}
	@include g.sp{
		dl{
			margin-bottom: g.sp_vw(25);
		}
		dt{
			@include g.fs(16, 28, true, false);
			@include g.ls(80);
			width: g.sp_vw(110);
			margin-right: g.sp_vw(15);
		}
		dd{
			@include g.fs(16, 28, true,false);
			@include g.ls(80);
		}
	}
}
