@charset "UTF-8";
////////////////////////
// top
////////////////////////

@use "sass:math";
@use '../global' as g;

.pg-top{
	position: relative;
	overflow-x: hidden;
}

.pg-top__copy{
	padding: g.pc_vw(180) g.pc_vw(56) g.pc_vw(80);
	&--en{
		display: block;
		@include g.font_pp_m;
		@include g.fs(80, 80, false);
		@include g.ls(30);
		margin-bottom: g.pc_vw(31);
	}
	&--jp{
		display: block;
		@include g.font_noto_m;
		@include g.fs(36, 56, false);
		@include g.ls(60);
	}
	@include g.sp{
		padding: g.sp_vw(140) g.sp_vw(23) g.sp_vw(80);
		&--en{
			@include g.fs(36, 36, false, false);
			@include g.ls(30);
			margin-bottom: g.sp_vw(22);
		}
		&--jp{
			display: block;
			@include g.fs(25, 29, false, false);
			@include g.ls(60);
		}
	}
}


// === about ==================
//

.pg-top__about{
	position: relative;
	// background-color: #000;
	padding: 0 g.pc_vw(50) g.pc_vw(300);
	@include g.sp{
		padding: 0 g.sp_vw(23) g.sp_vw(250);
	}
}

.pg-top__aboutMovieContainer{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: g.pc_vw(907);
	overflow: hidden;
	@include g.sp{
		height: g.sp_vw(600);
	}
	@include g.after{
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: g.pc_vw(694);
		background: linear-gradient(rgba(0,0,0,0),rgba(0,0,0,1));
		@include g.sp{
			height: g.sp_vw(442);
		}
	}
}

.pg-top__aboutImage{
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.pg-top__aboutMovie{
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	transition: opacity 1s;
	&.is-ready{
		opacity: 1;
	}
	@include g.pc{
		width: g.pc_vw(907 * math.div(16, 9));
		height: g.pc_vw(907);
	}
	@include g.sp{
		width: g.sp_vw(600 * math.div(16, 9));
		height: g.sp_vw(600);
	}
}

.pg-top__aboutContentsContainer{
	position: relative;
	z-index: 1;
	padding-top: g.pc_vw(476);
	@include g.sp{
		padding-top: g.sp_vw(415);
	}
}

.pg-top__aboutTitle{
	padding-bottom: g.pc_vw(30);
	border-bottom: 1px solid rgba($color: #FFF, $alpha: 0.2);
	margin-bottom: g.pc_vw(120);
	@include g.sp{
		padding-bottom: g.sp_vw(21);
		margin-bottom: g.sp_vw(60);
	}
}

.pg-top__aboutStatement{
	width: g.pc_vw(1109);
	margin-bottom: g.pc_vw(40);
	@include g.sp{
		width: 100%;
		margin-bottom: g.sp_vw(40);
	}
}

.pg-top__aboutStatementEn{
	display: block;
	@include g.font_pp_m;
	@include g.fs(56, 56, true);
	@include g.ls(30);
	margin-bottom: g.pc_vw(52);
	@include g.sp{
		padding-right: g.sp_vw(5);
		@include g.fs(30, 32, true, false);
		margin-bottom: g.sp_vw(46);
	}
}

.pg-top__aboutStatementJp{
	display: block;
	@include g.font_noto_m;
	@include g.fs(24, 35, true);
	@include g.ls(60);
	opacity: 0.4;
	@include g.sp{
		@include g.fs(18, 26, true, false);
	}
}

.pg-top__aboutDescription{
	display: flex;
	justify-content: flex-end;
	padding-right: g.pc_vw(115);
	@include g.sp{
		padding: 0;
	}
}

.pg-top__aboutDescriptionWrapper{
	width: g.pc_vw(312);
	@include g.sp{
		width: g.sp_vw(270)
	}
}

.pg-top__aboutDescriptionBody{
	@include g.font_noto_r;
	@include g.fs(14, 20, true);
	@include g.ls(100);
	opacity: 0.4;
	margin-bottom: g.pc_vw(60);
	text-align: justify;
	@include g.sp{
		@include g.fs(14, 20, true, false);
		margin-bottom: g.sp_vw(40);
	}
}

// === case study ==================
//

.pg-top__caseStudy{
	position: relative;
	padding: 0 g.pc_vw(50);
	padding-bottom: g.pc_vw(262);
	margin-bottom: g.pc_vw(28);
	// background: linear-gradient(rgba(0,0,0,1) 80%,rgba(0,0,0,0));
	@include g.sp{
		padding: 0 g.sp_vw(23);
		padding-bottom: g.sp_vw(150);
		margin-bottom: g.sp_vw(70);
	}
}

.pg-top__caseStudyTitle{
	position: relative;
	z-index: 1;
	padding-bottom: g.pc_vw(30);
	border-bottom: 1px solid rgba($color: #FFF, $alpha: 0.2);
	@include g.sp{
		padding-bottom: g.sp_vw(21);
	}
}

.pg-top__caseStudyWrapper{
	position: relative;
	@include g.pc{
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
}

.pg-top__caseStudyCenter{
	position: absolute;
	top: 50%;
	@include g.pc{
		left: g.pc_vw(327);
	}
	@include g.sp{
		right: 0;
	}
}

.pg-top__caseStudyImages{
	position: absolute;
	top: 50%;
	width: g.pc_vw(0);
	height: g.pc_vw(0);
	margin-top: g.pc_vw(0);
	border-radius: 50%;
	overflow: hidden;
	transition-duration: 0.8s;
	transition-property: width, height, left, right, margin-top;
	@include g.pc{
		left: g.pc_vw(327);
	}
	@include g.sp{
		width: g.sp_vw(0);
		height: g.sp_vw(0);
		margin-top: g.sp_vw(0);
		right: 0;
	}

	&.show{
		left: 0;
		margin-top: g.pc_vw(-327);
		width: g.pc_vw(654);
		height: g.pc_vw(654);
		@include g.sp{
			left: auto;
			right: g.sp_vw(-300);
			margin-top: g.sp_vw(-300);
			width: g.sp_vw(600);
			height: g.sp_vw(600);
		}
	}
}

.pg-top__caseStudyImagesWrapper{
	position: absolute;
	top: 50%;
	left: 50%;
	width: g.pc_vw(754);
	height: g.pc_vw(754);
	margin-top: g.pc_vw(-377);
	margin-left: g.pc_vw(-377);
	@include g.sp{
		width: g.sp_vw(600);
		height: g.sp_vw(600);
		margin-top: g.sp_vw(-300);
		margin-left: g.sp_vw(-300);
	}

	>div{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: center;
		background-size: cover;
	}
}

.pg-top__caseStudyLoopText{
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	margin-top: g.pc_vw(80);
	@include g.sp{
		margin-top: g.sp_vw(120);
	}
}

.pg-top__caseStudyLoopTextImg{
	width: g.pc_vw(4573);
	height: auto;
	@include g.sp{
		width: g.sp_vw(4573);
	}
}

.pg-top__caseStudyMoreBtn{
	position: relative;
	z-index: 1;
	width: g.pc_vw(312);
	@include g.sp{
		display: none;
	}
}

.pg-top__caseStudyList{
	position: relative;
	z-index: 1;
	width: g.pc_vw(882);
	@include g.sp{
		width: 100%;
	}
}

.pg-top__caseStudyListItem{
}

.pg-top__caseStudyListBtn{
	position: relative;
	text-decoration: none;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: g.pc_vw(26) 0 g.pc_vw(20);
	@include g.before{
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 1px;
		background: rgba(#fff,0.2);
		transform: translate3d(0,0,0);
	}
	@include g.sp{
		padding: g.sp_vw(23) 0 g.sp_vw(22);
	}
}

.pg-top__caseStudyListItemWrapper{
	@include g.pc{
		display: flex;
		align-items: center;
		width: g.pc_vw(800);
	}
	@include g.sp{
		width: g.sp_vw(270);
	}
}

.pg-top__caseStudyListItemCategory{
	@include g.font_pp_r;
	@include g.fs(12, 17, true);
	@include g.ls(30);
	width: g.pc_vw(228);
	flex-shrink: 0;
	padding-right: g.pc_vw(28);
	@include g.sp{
		@include g.fs(12, 17, true, false);
		width: 100%;
		margin-bottom: g.sp_vw(5);
	}
}

.pg-top__caseStudyListItemText{
	>span{
		display: block;
		@include g.font_pp_r;
		&:first-child{
			@include g.fs(24, 30, false);
			@include g.ls(30);
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
			@include g.pc{
				margin-bottom: g.pc_vw(8);
			}
			@include g.sp{
				@include g.fs(24, 29, true, false);
			}
		}
		&:last-child{
			@include g.fs(12, 17, false);
			@include g.ls(100);
			opacity: 0.4;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
			@include g.sp{
				display: none;
			}
		}
	}
}

.pg-top__caseStudyListItemArrow{
	position: relative;
	width: g.pc_vw(30);
	height: g.pc_vw(30);
	margin-top: g.pc_vw(16);
	@include g.sp{
		width: g.sp_vw(30);
		height: g.sp_vw(30);
		margin-top: 0;
	}
	i{
		display: block;
		width: 100%;
		height: 100%;
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/diagonal-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
}

.pg-top__caseStudyListItemLine{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 1px;
	background: g.$gradient;
	transform: scaleX(0);
	transform-origin: 0 0;
	z-index: 1;
}


// === career ==================
