		@include g.fs(64, 64, true);
		@include g.ls(30);
		margin-bottom: g.pc_vw(73);
		@include g.sp{
			@include g.font_pp_r;
			@include g.fs(30, 36, true, false);
			margin-bottom: g.sp_vw(26);
		}
	}

	&--jp{
		display: block;
		@include g.font_noto_m;
		@include g.fs(24, 35, true);
		@include g.ls(60);
		opacity: 0.4;
		@include g.sp{
			@include g.font_noto_r;
			@include g.fs(18, 26, true, false);
			@include g.ls(80);
		}
	}
}

.pg-top__careerMoreBtn{
	width: g.pc_vw(312);
	margin: 0 auto;
	@include g.sp{
		width: g.sp_vw(212);
	}
}


// === interview ==================
//

.pg-top__interview{
	margin-bottom: g.pc_vw(47);
	@include g.sp{
		margin-bottom: g.sp_vw(50);
	}
}

.pg-top__interviewTitle{
	margin-left: g.pc_vw(50);
