	}
}


// === job ==================
//

.pg-top__job{
	padding-top: g.pc_vw(270);
	padding-bottom: g.pc_vw(250);
	// background: linear-gradient(0deg, rgba(0,0,0,1) 70%,rgba(0,0,0,0));
	@include g.sp{
		padding-top: g.sp_vw(90);
		padding-bottom: g.sp_vw(80);
	}
}

.pg-top__jobContainer{
	@include g.pc{
		width: g.pc_vw(1110);
		margin: 0 auto;
	}
	@include g.sp{
		padding: 0 g.sp_vw(23);
	}
}

.pg-top__jobTitle{
	margin-bottom: g.pc_vw(20);
	@include g.sp{
		margin-bottom: g.sp_vw(38);
	}
}

.pg-top__jobListItemBtn{
	position: relative;
	text-decoration: none;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid rgba(#fff,0.2);
	padding: g.pc_vw(39) 0 g.pc_vw(35);
	&:first-child{
		border-top: 1px solid rgba(#fff,0.2);
	}
