@charset "UTF-8";
////////////////////////
// footer
////////////////////////

@use '../global' as g;

.l-footer{
	z-index: g.zIndex(footer);
	position: relative;
	background: #000;
	color: #fff;
	@include g.pc{
		display: flex;
		justify-content: space-between;
		padding: g.pc_vw(66) g.pc_vw(50) g.pc_vw(77);

	}
	@include g.sp{
		padding: g.sp_vw(47) g.sp_vw(g.$spMargin) g.sp_vw(49);
	}
}

.l-footer__col{
	@include g.pc{
		display: flex;
		&:nth-of-type(1){
			order:1;
		}
	}
	@include g.sp{
		&:nth-of-type(1){
			padding-bottom: g.sp_vw(40);
			border-bottom: 1px solid rgba(#fff, 0.2);
			margin-bottom: g.sp_vw(37);
		}
	}
}

.l-footer__logo{
  a{
    @include g.fs(0,0);
		@include g.hover{
			opacity: g.$fadeOpacity;
		}
		transition: opacity g.$fadeDuration;
		display:block;
	}
  svg{
    width: 100%;
    height :auto;
  }
	@include g.pc{
		width: g.pc_vw(118);
		margin: 0 g.pc_vw(112) 0 0;
	}
	@include g.sp{
		width: g.sp_vw(118);
		margin: 0 0 0 0;
	}
}

.l-footer__links{
  a{
		@include g.font_pp_r;
		@include g.hover{
			opacity: g.$fadeOpacity;
		}
		transition: opacity g.$fadeDuration;
		display:block;
    color:#fff;
    text-decoration: none;
  }
	@include g.pc{
		@include g.ls(30);
		&:nth-of-type(1){
			margin-right: g.pc_vw(25);
			li:not(:last-of-type){
				margin-bottom: g.pc_vw(3);
			}
			a{
				@include g.fs(18, 28, false);
			}
		}
		&:nth-of-type(2){
			li:not(:last-of-type){
				margin-bottom: g.pc_vw(3);
			}
			a{
				@include g.fs(14, 22, false);
			}
		}
	}
	@include g.sp{
		@include g.ls(30);
		&:nth-of-type(1){
			a{
				@include g.fs(18, 28, false, false);
			}
		}
		&:nth-of-type(2){
			a{
				@include g.fs(14, 26, false, false);
			}
		}
	}
}

.l-footer__nav{
	display: flex;
	@include g.pc{
		ul{
			&:not(:last-of-type){
				margin-right: g.pc_vw(25);
			}
		}
	}
	@include g.sp{
		ul{
			width: 50%;
		}
	}
}


.l-footer__address{
	@include g.pc{
		width: g.pc_vw(292);
	}
	@include g.sp{
		text-align: right;
	}
}

.l-footer__access{
	@include g.font_pp_r;
	@include g.pc{
		@include g.fs(16,22);
		@include g.ls(30);
		margin-bottom: g.pc_vw(13);
	}
	@include g.sp{
		@include g.fs(16,22,true,false);
		@include g.ls(30);
		margin-bottom: g.sp_vw(13);
	}
}

.l-footer__copyright{
	@include g.pc{
		@include g.fs(12,14);
		@include g.ls(100);
		padding: g.pc_vw(20) 0 g.pc_vw(30);
	}
	@include g.sp{
		@include g.fs(12,17,true,false);
		@include g.ls(30);
	}
}

.l-footer__btn{
	@include g.hover{
		opacity: 0.4;
	}
	transition: opacity 0.4s;
	margin-left: g.pc_vw(145);
	img{
		display: block;
		width: g.pc_vw(60);
		margin-bottom: g.pc_vw(16);
	}
	span{
		@include g.font_pp_r;
		@include g.fs(18, 24, true);
		@include g.ls(30);
		display: block;
		color: #fff;
	}
	@include g.sp{
		display:none;
	}
}
