@charset "UTF-8";
////////////////////////
// title
////////////////////////

@use '../../global' as g;

.c-title{
	display: flex;
	b{
		display: block;
		margin-right: g.pc_vw(20);
		@include g.before{
			width: g.pc_vw(20);
			height: g.pc_vw(20);
			border-radius: 50%;
			background: #fff;
			animation:icon-blink 4s g.$easeInOutSine infinite alternate;
		}
	}
	span{
		@include g.font_pp_r;
		@include g.fs(18,24);
		@include g.ls(30);
		flex:1;
	}
	@include g.sp{
		b{
			margin-right: g.sp_vw(10);
			@include g.before{
				width: g.sp_vw(15);
				height: g.sp_vw(15);
			}
		}
		span{
			@include g.fs(14,14,true,false);
			@include g.ls(30);
		}
	}
}
@keyframes icon-blink{
	0% {opacity:1.0;}
	100% {opacity:0.2;}
}

.c-title--vertical {
	@include g.sp{
		writing-mode: vertical-rl;
		b{
			display: block;
			margin: 0 0 g.sp_vw(10);
			text-align: center;
			&:before{
				display: inline-block;
			}
		}
		span{
			@include g.fs(18,18,true,false);
			@include g.ls(30);
		}
	}
}
