@charset "UTF-8";
////////////////////////
// sample
////////////////////////

@use '../global' as g;

.pg-sample{
	position: relative;
	z-index: 1;
	padding: g.pc_vw(200) g.pc_vw(50) g.pc_vw(100);
}

.pg-sample__title{
	@include g.fs(40, 40, true);
	border-bottom: 1px solid #fff;
	margin-bottom: g.pc_vw(50);
	padding-bottom: g.pc_vw(30);
	@include g.sp{
		@include g.fs(25, 25, true, false);
		margin-bottom: g.sp_vw(50);
		padding-bottom: g.sp_vw(30);
	}
}

.pg-sample__box{
	position: relative;
}

.pg-sample__contactBG{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.pg-sample__loopText{
	position: relative;
	z-index: 2;
	width: g.pc_vw(4731);
}

.pg-sample__section{
	padding: g.pc_vw(50) g.pc_vw(50);

}

.pg-sample__card{
	width: g.pc_vw(360);
	@include g.sp{
		width: g.sp_vw(268);
	}
}

.pg-sample__card2{
	@include g.pc{
		width: g.pc_vw(360);
	}
	@include g.sp{
	}
}

.pg-sample__cursorLink{
	display: inline-block;
	@include g.fs(20, 20, true);
	padding: g.pc_vw(80);
	margin-right: g.pc_vw(20);
	background-color: rgba($color: #FFF, $alpha: 0.1);
	@include g.sp{
		padding:g.sp_vw(40);
		margin-right: g.sp_vw(20);
		@include g.fs(20, 20, true, false);
	}
}

.pg-sample__section--cursor{
	@include g.sp{
		display: none;
	}
}
