@charset "UTF-8";


////////////////////////////
// 変数
////////////////////////////

// ブレイクポイント
$breakpoint: 900;

// グラデーション
$gradient: linear-gradient(0deg, rgba(229,85,31,1) 0%, rgba(222,6,108,1) 100%);

// hover fadeの時間
$fadeDuration: 0.3s;

// hover fadeの 透明度
$fadeOpacity: 0.4;

// pcのサイドマージン
$pcMargin: 280;

// spのサイドマージン
$spMargin: 20;

// z-indexが10以上の場合 設定
// zIndex(header); と読み込む
$zIndex: (
	cursor: 30,
	movieModal: 20,
  headerNav: 12,
	header: 11,
	footer: 10,
);

@function zIndex($key) {
  @return map-get($zIndex, $key);
}

