@charset "UTF-8";
////////////////////////
// loopText
////////////////////////

@use '../../global' as g;

.c-loopText{
	position: relative;
	display: block;
	width: 100%;
	overflow: hidden;
}

.c-loopText__inner{
	position: absolute;
	left: 0;
	top: 0;
	display: flex;

	.c-loopText__img{
		margin-right: g.pc_vw(360);
		@include g.sp{
			margin-right: g.sp_vw(180);
		}
	}
}

