@charset "UTF-8";
////////////////////////
// terms
////////////////////////
@use "sass:math";
@use '../../global' as g;

.p-terms{
	@include g.pc{
		padding: g.pc_vw(180) 0 g.pc_vw(100);
	}
	@include g.sp{
		padding: g.sp_vw(140) g.sp_vw(g.$spMargin) g.sp_vw(80);
	}
}

.p-terms__head{
	@include g.pc{
		padding: 0 g.pc_vw(50) g.pc_vw(85);
	}
	@include g.sp{
		padding: 0 0 g.sp_vw(45);
	}
}

.p-terms__title{
	border-bottom: 1px solid rgba(#fff,0.2);
	@include g.pc{
		padding-bottom: g.pc_vw(30);
	}
	@include g.sp{
		padding-bottom: g.sp_vw(20);
	}
}

.p-terms__subTitle{
	@include g.pc{
		@include g.ls(30);
		@include g.fs(24, 30, true);
		padding: 0 g.pc_vw(165) g.pc_vw(70);
	}
	@include g.sp{
		@include g.ls(80);
		@include g.fs(24, 30, true, false);
		padding: 0 0 g.sp_vw(90);
	}
}

.p-terms__body{
	@include g.pc{
		padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(150);
	}
	@include g.sp{
		padding: 0 0 g.sp_vw(80);
	}
}

.p-terms__section{
	@include g.pc{
		&:not(:last-of-type){
			margin-bottom: g.pc_vw(60);
		}
	}
	@include g.sp{
		&:not(:last-of-type){
			margin-bottom: g.sp_vw(80);
		}
	}
}

.p-terms__miniTitle{
	@include g.pc{
		@include g.fs(18, 24, true);
		@include g.ls(30);
		margin-bottom: g.pc_vw(20);
	}
	@include g.sp{
		@include g.fs(16, 22, true, false);
		@include g.ls(80);
		margin-bottom: g.sp_vw(20);
	}
}

.p-terms__text{
	color: #666;
	a{
		text-decoration: underline;
		@include g.hover{
			text-decoration: none;
		}
	}
	@include g.pc{
		@include g.fs(16, 30, true);
		@include g.ls(30);
	}
	@include g.sp{
		@include g.fs(14, 30, true, false);
		@include g.ls(100);
	}
}

.p-terms__btn{
	margin: 0 auto 0;
	@include g.pc{
		width: g.pc_vw(320);
	}
	@include g.sp{
		width: g.sp_vw(200);
	}
}

