@charset "UTF-8";
////////////////////////
// careers
////////////////////////

@use '../global' as g;

.pg-careers{
	position: relative;
}

.pg-careers__bg{
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-image: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/careers/kv.jpg);
	@include g.after{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(rgba(13, 13, 13, 0) 0%, rgba(13, 13, 13, 0.68) 50%, rgba(13, 13, 13, 0.91) 85.71%, rgba(0, 0, 0, 0.91) 100%);
	}
}


// kv
.pg-careers__kv{
	position: relative;
	overflow: hidden;
	height: g.pc_vw(900);
	display: flex;
	justify-content: flex-end;
	flex-direction: column;
	@include g.sp{
		height: g.sp_vw(700);
	}
}

.pg-careers__loop{
	position: absolute;
	left: g.pc_vw(0);
	overflow: hidden;
	width: 100%;
	opacity: 0;
	transition: opacity 0.5s;
	&.is-show{
		opacity: 1;
	}
	@include g.pc{
		bottom: g.pc_vw(20);
	}
	@include g.sp{
		bottom: g.sp_vw(0);
	}
}

.pg-careers__loopText{
	height: auto;
	@include g.pc{
		width: g.pc_vw(2800);
	}
	@include g.sp{
		width: g.sp_vw(1500);
	}
}

.pg-careers__kvInner{
	position: relative;
	z-index: 1;
	padding: 0 g.pc_vw(50) g.pc_vw(210);
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(100);
	}
}

.pg-careers__title{
	border-bottom: 1px solid rgba(#fff, 0.2);
	padding-bottom: g.pc_vw(35);
	margin-bottom: g.pc_vw(35);
	@include g.sp{
		padding-bottom: g.sp_vw(20);
		margin-bottom: g.sp_vw(30);
	}
}

.pg-careers__subTitle{
	span{
		display: block;
		&:nth-of-type(1){
			@include g.fs(82, 100, false);
			@include g.ls(30);
			@include g.font_pp_m;
			margin-bottom: g.pc_vw(20);
		}
		// &:nth-of-type(2){
		// 	@include g.fs(24,30, true);
		// 	@include g.ls(60);
		// 	opacity: g.$fadeOpacity;
		// }
	}
	@include g.sp{
		span{
			&:nth-of-type(1){
				@include g.fs(44, 52, false, false);
				@include g.ls(30);
				margin-bottom: g.sp_vw(20);
			}
			// &:nth-of-type(2){
			// 	@include g.fs(18,28, true, false);
			// 	@include g.ls(30);
			// }
		}
	}
}

// body
.pg-careers__body{
	position: relative;
	// background: #000;
}


// lead
.pg-careers__lead{
	@include g.pc{
		@include g.fs(18, 30, true);
		@include g.ls(80);
		position: relative;
		display: flex;
		justify-content: flex-end;
		padding: 0 g.pc_vw(100) g.pc_vw(120) 0;
		transform: translateY(g.pc_vw(-100));
	}
	@include g.sp{
		@include g.fs(18, 30, true, false);
		@include g.ls(80);
		// padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(180) g.sp_vw(80);
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(180) g.sp_vw(g.$spMargin);
		// br{
		// 	display: none;
		// }
	}
}

// interview
.pg-careers__interview{

}

.pg-careers__interviewTitle{

}

.pg-careers__interviewMain{
	@include g.pc{
		display: flex;
		padding: 0 g.pc_vw(85) g.pc_vw(250);
		flex-wrap: wrap;
		justify-content: space-between;
		@include g.after{
			width: g.pc_vw(360);
			height: 0;
		}
	}
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(75);
		.c-card__photo{
			opacity: 1;
		}
	}
}

.pg-careers__interviewCard{
	@include g.pc{
		width: g.pc_vw(360);
		padding: 0 0 g.pc_vw(100);
		&:nth-of-type(3n+2){
			padding: g.pc_vw(100) 0 0;
		}
	}
	@include g.sp{
			.pg-careers__interviewMain:not(.is-more) &{
			&:nth-child(n + 7) {
				display: none;
			}
		}
		position: relative;
		padding: g.sp_vw(11) 0;
		@include g.after{
			width: g.sp_vw(214);
			height: 1px;
			background: rgba(#fff,0.2);
			position: absolute;
			right: 0;
			bottom: 0;
		}
		&:not(:last-of-type){

		}
	}
}

.pg-careers__interviewBtn{
	@include g.pc{
		display: none;
	}
	@include g.sp{
		width: g.sp_vw(210);
		margin: 0 auto g.sp_vw(80);
	}
}

.pg-careers__interviewLink{

}


// job
.pg-careers__job{
	padding: 0 g.pc_vw(50) 0;
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) 0;
	}
}

.pg-careers__jobTitle{
	padding-bottom: g.pc_vw(30);
	@include g.sp{
		padding-bottom: g.sp_vw(20);
	}
}

.pg-careers__jobMain{
	@include g.sp{
		border-bottom: 1px solid rgba(#fff,0.2);
		padding-bottom: g.sp_vw(50);
	}
}

.pg-careers__jobSection{
	border-top: 1px solid rgba(#fff, 0.2);
	@include g.pc{
		display: flex;
		padding-bottom: g.pc_vw(80);
	}
	@include g.sp{
		padding-top: g.sp_vw(40);
	}
}

.pg-careers__jobHead{
	@include g.pc{
		width: g.pc_vw(335);
		padding-top: g.pc_vw(45);
		margin-right: g.pc_vw(120);
	}
	@include g.sp{

	}
}

.pg-careers__jobHeadTitle{
	@include g.pc{
		@include g.fs(32, 40, true);
		@include g.ls(30);
		margin-bottom: g.pc_vw(22);
	}
	@include g.sp{
		@include g.fs(32, 44, true, false);
		@include g.ls(30);
		margin-bottom: g.sp_vw(22);
	}
}

.pg-careers__jobHeadText{
	opacity: g.$fadeOpacity;
	@include g.pc{
		@include g.fs(18, 28, true);
		@include g.ls(80);
		margin-bottom: g.pc_vw(50);
	}
	@include g.sp{
		@include g.fs(18, 28, true, false);
		@include g.ls(80);
		margin-bottom: g.sp_vw(45);
	}
}

.pg-careers__jobHeadBtn{
	@include g.sp{
		margin-left: g.sp_vw(60);
	}
}

.pg-careers__jobBody{
	@include g.pc{
		flex:1;
	}
}

.pg-careers__jobDetail{
	&:not(:last-of-type){
		border-bottom: 1px solid rgba(#fff,0.2);
	}
	@include g.sp{
		margin-left: g.sp_vw(60);
	}
}

.pg-careers__jobName{
	@include g.fs(24, 34, false);
	@include g.ls(30);
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: g.pc_vw(45) 0;
	img{
		width: g.pc_vw(20);
		height: g.pc_vw(20);
		transition: transform 0.2s;
	}
	&.is-show{
		img{
			transform: rotate(45deg);
		}
	}
	@include g.sp{
		@include g.fs(18, 28, false, false);
		@include g.ls(30);
		padding: g.sp_vw(22) 0;
		img{
			width: g.sp_vw(15);
			height: g.sp_vw(15);
		}
	}
}

.pg-careers__jobInfo{
	padding-bottom: g.pc_vw(80);
	display: none;
	p{
		@include g.fs(16, 28, true);
		@include g.ls(80);
		margin-bottom: g.pc_vw(35);
	}
	dl{
		&:not(:last-of-type){
			margin-bottom: g.pc_vw(25);
		}
	}
	dt{
		@include g.fs(16, 28, true);
		@include g.ls(80);
		margin-bottom: g.pc_vw(18);
	}
	dd{
		@include g.fs(16, 28, true);
		@include g.ls(30);
		color:#666;
		a{
			color:#fff;
			text-decoration: underline;
		}
	}
	@include g.sp{
		padding-bottom: g.sp_vw(40);
		p{
			@include g.fs(16, 28, true, false);
			margin-bottom: g.sp_vw(35);
		}
		dl{
			&:not(:last-of-type){
				margin-bottom: g.sp_vw(25);
			}
		}
		dt{
			@include g.fs(16, 28, true, false);
			@include g.ls(80);
			margin-bottom: g.sp_vw(18);
		}
		dd{
			@include g.fs(16, 28, true, false);
			@include g.ls(30);
		}
	}
}

.pg-careers__jobEmpty{
	opacity: g.$fadeOpacity;
	@include g.pc{
		@include g.fs(16, 16, true);
		@include g.ls(80);
		height: 100%;
		display: flex;
		align-items: center;
	}
	@include g.sp{
		@include g.fs(16, 16, true, false);
		@include g.ls(80);
		text-align: center;
		padding: g.sp_vw(40) 0;
	}
}
