@charset "UTF-8";
////////////////////////
// articles
////////////////////////

@use '../../global' as g;

.p-articles{
	&--empty{
		@include g.fs(18, 18, true);
		@include g.ls(60);
		height: g.pc_vw(400);
		display: flex;
		align-items: center;
		justify-content: center;
		border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
		@include g.sp{
			@include g.fs(14, 14, true,false);
			height: g.sp_vw(200);
		}
	}
}
