@charset "UTF-8";
////////////////////////
// releases-detail
////////////////////////
@use "sass:math";
@use '../global' as g;

.pg-releases-detail{
	position: relative;
}

// kv
.pg-releases-detail__kv{
	background: linear-gradient(rgba(13, 13, 13, 0) 0%, rgba(13, 13, 13, 0.68) 50%, rgba(13, 13, 13, 0.91) 85.71%, rgba(0, 0, 0, 0.91) 100%);
	@include g.pc{
		min-height:  g.pc_vw(900);
		padding: g.pc_vw(180) g.pc_vw(50) 0;
	}
	@include g.sp{
		padding: g.sp_vw(60) 0 0;
	}
}

.pg-releases-detail__kvInner{
	position: relative;
	display: flex;
	@include g.pc{
		padding-bottom: g.pc_vw(80);
	}
	@include g.sp{
		flex-wrap:wrap;
		padding-bottom: g.sp_vw(40);
	}
}

.pg-releases-detail__kvLine{
	height: 1px;
	background: rgba($color: #fff, $alpha: 0.2);
	@include g.sp{
		margin: 0 g.sp_vw(g.$spMargin) 0;
	}
}

.pg-releases-detail__kvTitle{
	@include g.pc{
		padding-bottom: g.pc_vw(30);
		margin-bottom: g.pc_vw(60);
		border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	}
	@include g.sp{
		display: none;
	}
}

.pg-releases-detail__kvSpTitle{
	@include g.pc{
		display: none;
	}
	@include g.sp{
		width: 100%;
		padding-bottom: g.sp_vw(20);
		margin: 0 g.sp_vw(g.$spMargin) g.sp_vw(40);
		border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	}
}

.pg-releases-detail__time{
	time{
		display: block;
	}
	span{
		@include g.font_pp_r;
		display: block;
		&:nth-of-type(1){
			opacity: 0.4;
		}
		&:nth-of-type(3){
			position: relative;
			opacity: 0.4;
			@include g.after{
				height: 1px;
				transform: rotate(-45deg);
				background: #fff;
			}
		}
	}
	@include g.pc{
		width: g.pc_vw(115);
		time{
			margin: 0 g.pc_vw(0) g.pc_vw(0) g.pc_vw(30);
		}
		span{
			@include g.ls(30);
			text-align: center;
			&:nth-of-type(1){
				@include g.fs(12, 12, true);
				margin-bottom: g.pc_vw(8);
			}
			&:nth-of-type(2){
				@include g.fs(24, 24, true);
				margin-bottom: g.pc_vw(3);
			}
			&:nth-of-type(3){
				@include g.after{
					margin: g.pc_vw(10) auto;
					width: g.pc_vw(12);
				}
			}
			&:nth-of-type(4){
				@include g.fs(24, 24, true);
			}
		}
	}
	@include g.sp{
		width: g.sp_vw(80);
		time{
			margin: 0 g.sp_vw(0) g.sp_vw(0) g.sp_vw(g.$spMargin);
		}
		span{
			@include g.ls(30);
			&:nth-of-type(1){
				@include g.fs(12, 12, true, false);
				margin-bottom: g.sp_vw(8);
			}
			&:nth-of-type(2){
				@include g.fs(24, 24, true, false);
				margin-bottom: g.sp_vw(3);
			}
			&:nth-of-type(3){
				@include g.after{
					display: inline-block;
					margin: g.sp_vw(6) 0 g.sp_vw(3) g.sp_vw(8);
					width: g.sp_vw(15);
				}
			}
			&:nth-of-type(4){
				@include g.fs(24, 24, true, false);
			}
		}
	}
}

.pg-releases-detail__photo{
	position: relative;
	overflow: hidden;
	@include g.pc{
		width: g.pc_vw(768);
		order:1;
	}
	@include g.sp{
		width: 100%;
		margin-bottom: g.sp_vw(40);
	}
}

.pg-releases-detail__photoInner{
	position: relative;
	@include g.before{
		width: 100%;
		height: 0;
		padding-top: calc(9 / 16 * 100%);
	}
	img{
		position: absolute;
		left: 0;
		top:0;
		right: 0;
		bottom:0;
		margin:auto;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transition: opacity 0.3s, transform 0.3s;
	}
}

.pg-releases-detail__info{
	position: relative;
	flex:1;
	@include g.pc{
		order:2;
		padding: 0 0 0 g.pc_vw(30);
	}
	@include g.sp{
		width: 100%;
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(21) 0;
	}
}


.pg-releases-detail__category{
	@include g.fs(12,12,true);
	@include g.ls(30);
	opacity: 0.4;
	@include g.pc{
		margin-bottom: g.pc_vw(15);
	}
	@include g.sp{
		@include g.fs(12,12,true,false);
		margin-top: g.sp_vw(-2);
		margin-bottom: g.sp_vw(10);
	}
}

.pg-releases-detail__title{
	@include g.pc{
		@include g.fs(28,38,true);
		@include g.ls(60);
		margin-bottom: g.pc_vw(40);
	}
	@include g.sp{
		@include g.fs(24,38,true, false);
		@include g.ls(30);
		margin-bottom: g.sp_vw(40);
	}
}

.pg-releases-detail__description{
	@include g.ls(30);
	opacity: 0.4;
	@include g.pc{
		@include g.fs(16,24,true);
		margin: 0 g.pc_vw(100) 0 0;
	}
	@include g.sp{
		@include g.fs(16,24,true,false);
	}
}

.pg-releases-detail__links{
	margin-top: g.pc_vw(40);
	@include g.sp{
		margin-top: g.sp_vw(40);
	}
}



// body
.pg-releases-detail__body{
	background: rgba(0, 0, 0, 0.91);
	padding-top: g.pc_vw(80);
	@include g.sp{
		padding-top: g.sp_vw(80);
	}
}


// under
.pg-releases-detail__under{
	padding: g.pc_vw(60) 0 0;
	background: #000;
	@include g.sp{
		padding: 0 0 0;
	}
}

// staff
.pg-releases-detail__staff{
	padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(190);
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(80);
	}
}

// pager
.pg-releases-detail__pager{
	padding: 0 g.pc_vw(g.$pcMargin) g.pc_vw(140);
	@include g.sp{
		padding: 0 g.sp_vw(80) g.sp_vw(80);
	}
}

// related
.pg-releases-detail__related{
	padding: 0 g.pc_vw(50) 0;
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) g.sp_vw(0);
	}
}

.pg-releases-detail__relatedTitle{
	padding-bottom: g.pc_vw(30);
	border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	@include g.sp{
		padding-bottom: g.sp_vw(20);
	}
}



