@charset "UTF-8";
////////////////////////
// about
////////////////////////
@use "sass:math";
@use '../global' as g;

.pg-about{
	position: relative;
}

.pg-about__inner{
	z-index: g.zIndex(1);
	position: relative;
	background: #000;
}

// kv
.pg-about__kv{
	overflow: hidden;
	position: relative;
	height: g.pc_vw(600);
	@include g.after{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(#000,0.2);
	}
	@include g.sp{
		height: g.sp_vw(600);
	}
}

// kvMovie
.pg-about__kvVideo{
	position: absolute;
	opacity: 0;
	transition: opacity 1s;
	&.is-ready{
		opacity: 1;
	}
	@include g.pc{
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 100%; // 1440
		height: g.pc_vw(math.div(1440 * 9, 16));
	}
	@include g.sp{
		left: 50%;
		top: 0;
		transform: translateX(-50%);
		width: g.sp_vw(math.div(600 * 16, 9));
		height: 100%; // 600
	}
}

// kvImage
.pg-about__kvImage{
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

// layer
.pg-about__layer{
	width: 100%;
	height: g.pc_vw(600);
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1;
	@include g.sp{
		height: g.sp_vw(600);
	}
}

// lead
.pg-about__lead{
	position: relative;
	padding: 0 g.pc_vw(80) 0 g.pc_vw(50);
	margin: g.pc_vw(-230) 0 g.pc_vw(210);
	@include g.sp{
		padding: 0 g.sp_vw(g.$spMargin) 0;
		margin: g.sp_vw(-130) 0 g.sp_vw(150);
	}
}

.pg-about__leadTitle{
	@include g.font_pp_m;
	@include g.fs(100, 100, true);
	@include g.ls(30);
	margin: 0 0 g.pc_vw(50);
	@include g.sp{
		@include g.fs(48, 48, true, false);
		margin: 0 0 g.sp_vw(30);
	}
}

.pg-about__leadSubTitle{
	@include g.fs(48, 56, true);
	@include g.ls(60);
	@include g.sp{
		@include g.fs(28, 38, true, false);
		margin: 0 0 g.sp_vw(45);
	}
}

.pg-about__leadText{
	@include g.fs(18, 32, true);
	@include g.ls(80);
	padding: 0 0 0 g.pc_vw(670);
	@include g.sp{
		@include g.fs(16, 28, true, false);
		@include g.ls(30);
		padding: 0 0 0;
	}
}

// business
.pg-about__business{
	margin: 0 g.pc_vw(50) g.pc_vw(360);
	@include g.sp{
		margin: 0 g.sp_vw(g.$spMargin) g.sp_vw(155);
	}
}

.pg-about__businessBody{
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	@include g.pc{
		a{
			text-decoration: underline;
		}
		margin-top: g.pc_vw(30);
		dl{
			display: flex;
			align-items: center;
			height:  g.pc_vw(147);
			border-bottom: 1px solid rgba(#fff,0.2);
			&:first-of-type{
				border-top: 1px solid rgba(#fff,0.2);
			}
		}
		dt{
			@include g.font_pp_r;
			@include g.fs(32, 38, true);
			@include g.ls(30);
			width: g.pc_vw(530);
		}
		dd{
			@include g.fs(16, 26, true);
			@include g.ls(30);
			flex:1;
		}
	}
	@include g.sp{
		margin-top: g.sp_vw(30);
		dl{
			padding: g.sp_vw(40) 0;
			border-bottom: 1px solid rgba(#fff,0.2);
			&:first-of-type{
				border-top: 1px solid rgba(#fff,0.2);
			}
		}
		dt{
			@include g.font_pp_r;
			@include g.fs(24, 30, true,false);
			@include g.ls(30);
			margin-bottom: g.sp_vw(17);
		}
		dd{
			@include g.fs(14, 26, true, false);
			@include g.ls(30);
		}
	}
}

.pg-about__businessChart{
	@include g.pc{
		width: g.pc_vw(800);
		margin: 0 auto 0;
	}
	@include g.sp{
		width: 100vw;
		margin-left: -1 * g.sp_vw(g.$spMargin);
	}
}

// company
.pg-about__company{
	display: flex;
	@include g.pc{
		width: g.pc_vw(1110);
		margin: 0 auto;
		padding-bottom: g.pc_vw(190);
	}
	@include g.sp{
		margin: 0 g.sp_vw(g.$spMargin) g.sp_vw(0);
		padding-bottom: g.sp_vw(50);
	}
}

.pg-about__companyHead{
	@include g.pc{
		padding-top: g.pc_vw(52);
		width: g.pc_vw(320);
	}
	@include g.sp{
		width: g.sp_vw(60);
	}
}

.pg-about__companyBody{
	@include g.pc{
		flex:1;
		a{
			text-decoration: underline;
		}
		dl{
			display: flex;
			align-items: center;
			height: g.pc_vw(120);
			border-bottom: 1px solid rgba(#fff,0.2);
			&:first-of-type{
				border-top: 1px solid rgba(#fff,0.2);
			}
		}
		dt{
			@include g.font_pp_r;
			@include g.fs(18, 24, true);
			@include g.ls(30);
			width: g.pc_vw(225);
		}
		dd{
			@include g.fs(16, 30, true);
			@include g.ls(30);
			flex:1;
		}
	}
	@include g.sp{
		dl{
			padding: g.sp_vw(40) 0;
			border-bottom: 1px solid rgba(#fff,0.2);
			&:first-of-type{
				border-top: 1px solid rgba(#fff,0.2);
			}
		}
		dt{
			@include g.font_pp_r;
			@include g.fs(24, 30, true, false);
			@include g.ls(30);
			margin-bottom: g.sp_vw(10);
		}
		dd{
			@include g.fs(14, 20, true, false);
			@include g.ls(30);
		}
	}
}

// contact
.pg-about__contact{
	position: relative;
	overflow: hidden;
	padding-top: g.pc_vw(60);
	height: g.pc_vw(600);
	@include g.sp{
		padding-top: g.sp_vw(80);
		height: g.sp_vw(600);
	}
}

.pg-about__loop{
	position: absolute;
	left: g.pc_vw(0);
	overflow: hidden;
	width: 100%;
	@include g.pc{
		bottom: g.pc_vw(-100);
	}
	@include g.sp{
		bottom: g.sp_vw(140);
	}
}

.pg-about__loopText{
	height: auto;
	@include g.pc{
		width: g.pc_vw(2591);
	}
	@include g.sp{
		width: g.sp_vw(1500);
	}
}

.pg-about__contactBtns{
	position: relative;
	@include g.pc{
		display: flex;
		justify-content: space-between;
		width: g.pc_vw(1110);
		margin: g.pc_vw(160) auto 0;
		li{
			width: g.pc_vw(530);
		}
	}
	@include g.sp{
		margin: g.sp_vw(136) g.sp_vw(g.$spMargin) 0;
		li{
			&:not(:last-of-type){
				margin-bottom: g.sp_vw(45);
			}
		}
	}
}

