@charset "UTF-8";
////////////////////////
// header
////////////////////////

@use '../global' as g;

.l-header{
	position: fixed;
	left: 0;
	top: 0;
	z-index: g.zIndex(header);
  width: 100%;
  height: g.pc_vw(100);
	display: flex;
	align-items:center;
	justify-content: space-between;
	@include g.sp{
		width: g.sp_vw(44);
		height: g.sp_vw(60);
		left: g.sp_vw(g.$spMargin);
		justify-content: flex-start;
	}
}

.l-header__logo{
  margin-left: g.pc_vw(50);
  a{
		@include g.hover{
			opacity: g.$fadeOpacity;
		}
		transition: opacity g.$fadeDuration;
		width: g.pc_vw(88);
		display: block;
		font-size: 0;
  }
  svg{
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
	@include g.sp{
		margin-left: 0;
		a{
			width: 100%;
		}
	}
}

//////////////////////////////
// PC only
//////////////////////////////

.l-header__nav{
	margin-right: g.pc_vw(50);
  ul{
		display: flex;
  }
  li:not(:last-of-type){
    margin-right: g.pc_vw(57);
  }
  a{
    @include g.fs(18,24,false);
		@include g.ls(30);
		@include g.hover{
			opacity: g.$fadeOpacity;
		}
		transition: opacity g.$fadeDuration;
		display: block;
    font-weight: bold;
    color: #fff;
    text-decoration: none;
  }
	@include g.sp{
		display:none;
	}
}

//////////////////////////////
// SP only
//////////////////////////////

.l-header__spLogo{
	width: g.sp_vw(88);
	margin-bottom: g.sp_vw(20);
}

.l-header__btn{
	@include g.pc{
		display: none;
	}
	position: fixed;
	z-index: g.zIndex(header);
	right: g.sp_vw(15);
	top: g.sp_vw(17);
	width: g.sp_vw(30);
	height: g.sp_vw(30);
	display: flex;
	align-items: center;
	justify-content: center;
	span{
		border-radius: 50%;
		width: g.sp_vw(14);
		height: g.sp_vw(14);
		background: #fff;
	}
}

.l-header__spNav{
	display: none;
	@include g.sp{
		&.is-show {
			display: flex;
		}
	}
	background: #222222;
	position: fixed;
	z-index: g.zIndex(headerNav);
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	justify-content: space-between;
	flex-direction: column;
}

.l-header__spUpper{
	padding: g.sp_vw(23) g.sp_vw(g.$spMargin) g.sp_vw(60);
}

.l-header__close{
	cursor: pointer;
	position: absolute;
	right: g.sp_vw(24);
	top: g.sp_vw(24);
	width: g.sp_vw(49);
	height: g.sp_vw(49);
}

.l-header__access{
	@include g.fs(16, 22, true, false);
	@include g.ls(30);
	margin-bottom: g.sp_vw(15);
}

.l-header__copyright{
	@include g.fs(10, 17, true, false);
	@include g.ls(30);
}

.l-header__spUnder{
	padding: g.sp_vw(23) g.sp_vw(g.$spMargin) g.sp_vw(100);
}

.l-header__largeLink{
	margin-bottom: g.sp_vw(30);
	li{
		border-bottom: 1px solid rgba(#000,0.2);
		&:first-of-type{
			border-top: 1px solid rgba(#000,0.2);
		}
	}

	a{
		@include g.font_pp_r;
		@include g.fs(46, 46, false, false);
		@include g.ls(30);
		padding: g.sp_vw(16) 0;
		text-decoration: none;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	i{
		width: g.sp_vw(25);
		height: g.sp_vw(25);
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/diagonal-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
}

.l-header__smallLink{
	display: flex;
	li{
		&:not(:last-of-type){
			margin-right: g.sp_vw(30);
		}
	}
	a{
		@include g.font_pp_r;
		@include g.fs(16, 22, true, false);
		@include g.ls(30);
		text-decoration: none;
	}
}
