@charset "UTF-8";
////////////////////////
// card
////////////////////////

@use '../../global' as g;

.c-card{
	@include g.before{
		width: 100%;
		padding-top: calc( (510 / 360 ) * 100% );
	}
	position: relative;
	width: 100%;
	overflow: hidden;
	display: block;
	@include g.sp{
		&--normal{

		}
		&--col{
			display: flex;
			align-items: center;
			&:before{
				display: none;
			}
		}
	}
}

.c-card__photo{
	position: absolute;
	left:0;
	top:0;
	width: 100%;
	height: 100%;
	opacity: 0.5;
	transition: opacity 0.2s;
	img{
		object-fit: cover;
		width: 100%;
		height: 100%;
		border-radius: g.pc_vw(25);
	}
	@include g.pc{
		.c-card:hover &{
			opacity: 0.8;
		}
	}
	@include g.sp{
		img{
			border-radius: g.sp_vw(15);
		}
		.is-focus &{
			opacity: 0.8;
		}
		.c-card--normal &{

		}
		.c-card--col &{
			position: relative;
			width: g.sp_vw(94);
		}
	}
}

.c-card__detail{
	position: absolute;
	left:0;
	top: 0;
	height: 100%;
	padding: 0 g.pc_vw(60) g.pc_vw(30) g.pc_vw(30);
	display: flex;
	justify-content: flex-end;
	flex-direction: column;
	@include g.sp{
		.c-card--normal &{
			padding: 0 g.sp_vw(60) g.sp_vw(23) g.sp_vw(g.$spMargin);
		}
		.c-card--col &{
			flex:1;
			position: relative;
			padding: g.sp_vw(10) g.sp_vw(20) g.sp_vw(10) g.sp_vw(22);
		}
	}
}

.c-card__title{
	user-select: none;
	@include g.fs(28, 38, true);
	@include g.ls(30);
	@include g.font_pp_r;
	margin-bottom: g.pc_vw(8);
	@include g.sp{
		@include g.fs(20, 28, true, false);
		@include g.ls(30);
		.c-card--normal &{
			margin-bottom: g.sp_vw(5);
		}
		.c-card--col &{
			margin-bottom: g.sp_vw(10);
		}
	}
}

.c-card__position{
	user-select: none;
	@include g.fs(18, 24, true);
	@include g.ls(30);
	@include g.font_pp_r;
	margin-bottom: g.pc_vw(10);
	@include g.sp{
		@include g.fs(13, 26, true, false);
		@include g.ls(30);
		.c-card--normal &{
			margin-bottom: g.sp_vw(7);
		}
		.c-card--col &{
			margin-bottom: g.sp_vw(10);
		}
	}
}

.c-card__category{
	user-select: none;
	@include g.fs(12, 18, true);
	@include g.ls(30);
	@include g.font_pp_r;
	@include g.sp{
		@include g.ls(30);
		@include g.fs(8, 12, true, false);
		.c-card--normal &{
			//
		}
		.c-card--col &{
			//
		}
	}
}

.c-card__arrow{
	user-select: none;
	position: absolute;
	right: g.pc_vw(30);
	bottom: g.pc_vw(30);
	width: g.pc_vw(20);
	height: g.pc_vw(20);
	i{
		display: block;
		width: 100%;
		height: 100%;
		background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/diagonal-arrow.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}
	@include g.sp{
		.c-card--normal &{
			right: g.sp_vw(g.$spMargin);
			bottom: g.sp_vw(23);
			width: g.sp_vw(20);
			height: g.sp_vw(20);
		}
		.c-card--col &{
			right: g.sp_vw(0);
			bottom: g.sp_vw(10);
			width: g.sp_vw(10);
			height: g.sp_vw(10);
		}
	}
}
