@charset "UTF-8";
////////////////////////
// works
////////////////////////

@use '../global' as g;

.pg-works{
	padding-bottom: g.pc_vw(120);
}

// case-study
.pg-works__case-study{
	position: relative;
	overflow: hidden;
	@include g.pc{
		height: g.pc_vw(945);
	}
	@include g.sp{
	}
}

.pg-works__case-studyLoader{
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
}

.pg-works__case-studyTitle{
	@include g.sp{
		padding-bottom: g.sp_vw(20);
	}
}

.pg-works__slidersUpper{
	z-index: 2;
	display: flex;
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	position: absolute;
	@include g.pc{
		width: g.pc_vw(710 - 50);
		height: g.pc_vw(50);
		left: g.pc_vw(50);
		top: g.pc_vw(245);
	}
	@include g.sp{
		top: g.sp_vw(352);
		left: g.sp_vw(g.$spMargin);
		width: g.sp_vw(375 - g.$spMargin * 2);
	}
}

.pg-works__indicator{
	display: flex;
	width: fit-content;
}

.pg-works__indicatorChild{
	position: relative;
	z-index: 1;
	span{
		@include g.font_pp_r;
		@include g.ls(30);
		opacity: 0.2;
		transition: 0.2s opacity;
	}
	@include g.hover{
		span{
			opacity: 1;
		}
	}
	&.is-current{
		span{
			opacity: 1;
		}
	}
	@include g.pc{
		padding: 0 g.pc_vw(11);
		span{
			@include g.fs(12, 17, true);
		}
	}
	@include g.sp{
		padding: 0 g.sp_vw(11);
		span{
			@include g.fs(12, 17, true,false);
		}
	}
}

.pg-works__indicatorLine{
	position: absolute;
	left:0;
	bottom: -1px;
	width: 100%;
	height: 1px;
	transition: 0.2s opacity;
	background: g.$gradient;
	transform-origin: 0 center !important;
	transform: scaleX(0);
}

.pg-works__sliders{
	height: 100%;
}

.pg-works__slider{
	height: 100%;
	position: relative;
	opacity: 1 !important;
	transition: none !important;
	&.swiper-slide-active{
		z-index: 3;
	}
}

.pg-works__sliderCircle{
	position: absolute;
	overflow: hidden;
	border-radius: 50%;
	opacity: 0;
	img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	@include g.pc{
		bottom: 0;
		right: g.pc_vw(-180);
		width: g.pc_vw(1000);
		height: g.pc_vw(1000);
	}
	@include g.sp{
		top:0;
		right: g.sp_vw(-25);
		width: g.sp_vw(400);
		height: g.sp_vw(400);
	}
}

.pg-works__sliderBody{
	position: relative;
	@include g.pc{
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		width: g.pc_vw(710);
		height: 100%;
		padding: 0 0 g.pc_vw(85) g.pc_vw(50);
	}
	@include g.sp{
		padding: g.sp_vw(420) g.sp_vw(g.$spMargin) 0;
	}
}

.pg-works__sliderCategory{
	@include g.font_pp_r;
	@include g.ls(30);
	opacity: 0;
	@include g.pc{
		@include g.fs(18, 24, true);
		margin-bottom: g.pc_vw(20);
	}
	@include g.sp{
		@include g.fs(18, 24, true, false);
		margin-bottom: g.sp_vw(20);
	}
}

.pg-works__sliderTitle{
	@include g.font_pp_m;
	opacity: 0;
	@include g.ls(30);
	@include g.pc{
		@include g.fs(51.84, 59.6, true);
	}
	@include g.sp{
		@include g.fs(32, 38, true, false);
		margin-bottom: g.sp_vw(40);
	}
}

.pg-works__sliderUnder{
	opacity: 0;
	@include g.pc{
		margin-top: g.pc_vw(70);
		display: flex;
		align-items: flex-end;
	}
	@include g.sp{
		margin-left: g.sp_vw(80);
	}
}

.pg-works__sliderDescription{
	@include g.ls(100);
	opacity: 0.4;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	@include g.pc{
		@include g.fs(14, 22, true);
		width: g.pc_vw(310);
		margin-right: g.pc_vw(30);
		-webkit-line-clamp: 4;
	}
	@include g.sp{
		@include g.fs(14, 22, true, false);
		margin-bottom: g.sp_vw(40);
		-webkit-line-clamp: 3;
	}
}

.pg-works__sliderBtn{
	@include g.pc{
		width: g.pc_vw(310);
	}
}

// release　//////////////////////////

.pg-works__release{
	padding-top: g.pc_vw(160);
	margin: 0 g.pc_vw(50) 0;
	@include g.sp{
		padding-top: g.sp_vw(160);
		margin: 0 g.sp_vw(g.$spMargin) g.sp_vw(60);
	}
}

.pg-works__releaseHead{
	@include g.pc{
		display: flex;
		justify-content: space-between;
	}
	@include g.sp{

	}
}

.pg-works__releaseTitle{
	border-bottom: 1px solid rgba(#fff, 0.2);
	@include g.pc{
		height: g.pc_vw(50);
		width: g.pc_vw(880);
	}
	@include g.sp{
		height: g.sp_vw(40);
	}
}

.pg-works__releaseSelects{
	@include g.pc{
		display: flex;
		justify-content: space-between;
		width: g.pc_vw(430);
	}
	@include g.sp{
		margin-left: g.sp_vw(55);
	}
}

.pg-works__releaseSelect{
	position: relative;

	border-bottom: 1px solid rgba(#fff, 0.2);
	select{
		@include g.font_pp_r;
		position: relative;
		z-index: 1;
		appearance: none;
		outline: none;
		border:none;
		background: transparent;
		color: #fff;
		width: 100%;
		height: 100%;

	}
	img{
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
	}
	@include g.pc{
		width: g.pc_vw(200);
		select{
			@include g.fs(18, 20, false);
			@include g.ls(30);
			padding-right: g.pc_vw(20);
		}
		img{
			width: g.pc_vw(10);
			height: g.pc_vw(10);
		}
	}
	@include g.sp{
		select{
			@include g.fs(14, 16, false, false);
			@include g.ls(30);
			padding: g.sp_vw(25) g.sp_vw(20) g.sp_vw(25) 0;
		}
		img{
			width: g.sp_vw(10);
			height: g.sp_vw(10);
		}
	}
}

.pg-works__releaseBody{

}


.pg-works__pager{
	margin-top: g.pc_vw(45);
	@include g.sp{
		margin: g.sp_vw(60) auto 0;
		width: g.sp_vw(210);
	}
}
