@charset "UTF-8";
////////////////////////
// pagerAround
////////////////////////

@use '../../global' as g;

.c-pagerAround{
  display: flex;
	justify-content: center;
	align-items: center;
	i{
		width: g.pc_vw(10);
		height: g.pc_vw(10);
		display: block;
	}
	b{
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 1px;
		background: g.$gradient;
		position: absolute;
		transform: scaleX(0);
		transform-origin: 0 0;
	}
	@include g.sp{
		justify-content: space-between;
		i{
			width: g.sp_vw(13);
			height: g.sp_vw(13);
		}
	}
}

.c-pagerAround__link{
	@include g.fs(18,18,false);
	@include g.ls(30);
	@include g.font_pp_r;
	position: relative;
	width: g.pc_vw(85);
	display: flex;
	align-items: center;
	justify-content: space-between;
	border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
	padding-bottom: g.pc_vw(16);
	&.is-disabled{
		opacity: 0.4;
		pointer-events: none;
	}
	&:not(:last-of-type){
		margin-right: g.pc_vw(30);
	}
	i{
		display: block;
	}
	&--back{
		i{
			background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/left-arrow.svg) center no-repeat;
			background-size: 100% auto;
		}
	}
	&--next{
		i{
			background: url(/wp-content/themes/theme/o-e-n.co.jp/_assets/images/common/right-arrow.svg) center no-repeat;
			background-size: 100% auto;
		}
	}
	@include g.sp{
		@include g.fs(18,18,false,false);
		@include g.ls(30);
		width: calc( (100% - g.sp_vw(20)) / 2 );
		padding-bottom: g.sp_vw(16);
	}
}
